@media screen and (max-width: 1199px) {
  .hide-onpad {
    display: none;
  }
  .dnb-promo {
    flex-direction: column;
  }
  .dnb-promo-side {
    display: flex;
    width: 100%;
    padding-left: 0 !important;
    padding-top: 10px;
    justify-content: space-between;
  }
  .dnb-promo-side-item {
    height: 154px;
  }
  .dnb-promo-side > * {
    width: 32.6%;
    margin-bottom: 0 !important;
  }
}
@media screen and (max-width: 1080px) {
  .dnb-media {
    flex-direction: column;
    align-items: center;
  }
  .dnb-media > aside {
    margin-top: 50px;
  }
}
@media screen and (max-width: 630px) {
  .hide-onfone {
    display: none;
  }
}
